import * as crypto from 'crypto';

export interface DeviceInfo {
  deviceId: string;
  deviceFingerprint: string;
  userAgent: string;
  screenResolution: string;
  timezone: string;
  language: string;
  ipAddress?: string;
  location?: string;
  deviceType?: string;
  browser?: string;
  browserVersion?: string;
}

/**
 * –ì–µ–Ω–µ—Ä–∞—Ü–∏—è —É–Ω–∏–∫–∞–ª—å–Ω–æ–≥–æ ID —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞
 */
export function generateDeviceId(): string {
  const userAgent = navigator.userAgent;
  const platform = navigator.platform;
  const language = navigator.language;
  const timezone = Intl.DateTimeFormat().resolvedOptions().timeZone;
  
  // –ò—Å–ø–æ–ª—å–∑—É–µ–º –±–æ–ª–µ–µ —Å—Ç–∞–±–∏–ª—å–Ω—ã–µ —Ö–∞—Ä–∞–∫—Ç–µ—Ä–∏—Å—Ç–∏–∫–∏ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞
  const data = `${platform}-${language}-${timezone}-${Date.now()}`;
  
  // –ü—Ä–æ—Å—Ç–æ–π —Ö–µ—à
  let hash = 0;
  for (let i = 0; i < data.length; i++) {
    const char = data.charCodeAt(i);
    hash = ((hash << 5) - hash) + char;
    hash = hash & hash; // Convert to 32bit integer
  }
  return Math.abs(hash).toString(36);
}

/**
 * –ì–µ–Ω–µ—Ä–∞—Ü–∏—è fingerprint —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ —Å —É—á–µ—Ç–æ–º –±—Ä–∞—É–∑–µ—Ä–∞
 */
export function generateDeviceFingerprint(): string {
  const userAgent = navigator.userAgent;
  const platform = navigator.platform;
  const language = navigator.language;
  const timezone = Intl.DateTimeFormat().resolvedOptions().timeZone;
  
  // –û–ø—Ä–µ–¥–µ–ª—è–µ–º —Ç–∏–ø –±—Ä–∞—É–∑–µ—Ä–∞
  const browserInfo = getBrowserInfo();
  
  const data = `${platform}-${language}-${timezone}-${browserInfo.browser}-${browserInfo.version}`;
  
  let hash = 0;
  for (let i = 0; i < data.length; i++) {
    const char = data.charCodeAt(i);
    hash = ((hash << 5) - hash) + char;
    hash = hash & hash;
  }
  return Math.abs(hash).toString(36);
}

/**
 * –û–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ –±—Ä–∞—É–∑–µ—Ä–µ
 */
function getBrowserInfo(): { browser: string; version: string } {
  const userAgent = navigator.userAgent;
  
  if (userAgent.includes('Chrome')) {
    return { browser: 'Chrome', version: getVersion(userAgent, 'Chrome') };
  } else if (userAgent.includes('Firefox')) {
    return { browser: 'Firefox', version: getVersion(userAgent, 'Firefox') };
  } else if (userAgent.includes('Safari') && !userAgent.includes('Chrome')) {
    return { browser: 'Safari', version: getVersion(userAgent, 'Safari') };
  } else if (userAgent.includes('Edge')) {
    return { browser: 'Edge', version: getVersion(userAgent, 'Edge') };
  } else if (userAgent.includes('Opera')) {
    return { browser: 'Opera', version: getVersion(userAgent, 'Opera') };
  } else {
    return { browser: 'Unknown', version: 'Unknown' };
  }
}

/**
 * –ò–∑–≤–ª–µ—á–µ–Ω–∏–µ –≤–µ—Ä—Å–∏–∏ –±—Ä–∞—É–∑–µ—Ä–∞
 */
function getVersion(userAgent: string, browserName: string): string {
  const match = userAgent.match(new RegExp(`${browserName}/([\\d.]+)`));
  return match ? match[1] : 'Unknown';
}

/**
 * –û–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ —Ç–∏–ø–∞ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞
 */
function getDeviceType(): string {
  const userAgent = navigator.userAgent;
  
  if (/Mobile|Android|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(userAgent)) {
    return 'mobile';
  } else if (/Tablet|iPad/i.test(userAgent)) {
    return 'tablet';
  } else {
    return 'desktop';
  }
}

/**
 * –ü–æ–ª—É—á–µ–Ω–∏–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ–± —É—Å—Ç—Ä–æ–π—Å—Ç–≤–µ
 */
export function getDeviceInfo(): DeviceInfo {
  const userAgent = navigator.userAgent;
  const platform = navigator.platform;
  const language = navigator.language;
  const timezone = Intl.DateTimeFormat().resolvedOptions().timeZone;
  const deviceType = getDeviceType();
  const browserInfo = getBrowserInfo();
  
  return {
    deviceId: generateDeviceId(),
    deviceFingerprint: generateDeviceFingerprint(),
    userAgent,
    screenResolution: 'unknown',
    timezone,
    language,
    deviceType,
    browser: browserInfo.browser,
    browserVersion: browserInfo.version,
  };
}

/**
 * –ü–æ–ª—É—á–µ–Ω–∏–µ IP –∞–¥—Ä–µ—Å–∞ (—á–µ—Ä–µ–∑ –≤–Ω–µ—à–Ω–∏–π —Å–µ—Ä–≤–∏—Å)
 */
export async function getIpAddress(): Promise<string> {
  try {
    const response = await fetch('https://api.ipify.org?format=json');
    const data = await response.json();
    return data.ip;
  } catch (error) {
    console.warn('–ù–µ —É–¥–∞–ª–æ—Å—å –ø–æ–ª—É—á–∏—Ç—å IP –∞–¥—Ä–µ—Å:', error);
    return 'unknown';
  }
}

/**
 * –ü–æ–ª—É—á–µ–Ω–∏–µ –≥–µ–æ–ª–æ–∫–∞—Ü–∏–∏ (–≥–æ—Ä–æ–¥/—Å—Ç—Ä–∞–Ω–∞)
 */
export async function getLocation(): Promise<string> {
  try {
    const response = await fetch('https://ipapi.co/json/');
    const data = await response.json();
    return `${data.city}, ${data.country_name}`;
  } catch (error) {
    console.warn('–ù–µ —É–¥–∞–ª–æ—Å—å –ø–æ–ª—É—á–∏—Ç—å –≥–µ–æ–ª–æ–∫–∞—Ü–∏—é:', error);
    return 'unknown';
  }
}

/**
 * –ü–æ–ª—É—á–µ–Ω–∏–µ –ø–æ–ª–Ω–æ–π –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ–± —É—Å—Ç—Ä–æ–π—Å—Ç–≤–µ
 */
export async function getFullDeviceInfo(): Promise<DeviceInfo> {
  const baseInfo = getDeviceInfo();
  const [ipAddress, location] = await Promise.all([
    getIpAddress(),
    getLocation(),
  ]);
  
  return {
    ...baseInfo,
    ipAddress,
    location,
  };
} 